






































































































import { Component, Vue, Prop, Watch } from 'vue-property-decorator';
import Quagga from 'quagga';
import VueBarcode from 'vue-barcode';
import { v4 as uuidv4 } from 'uuid';
import SnackBar from '@/components/SnackBar.vue';
import { mapState } from 'vuex';


@Component({
  components: {
    Quagga,
    VueBarcode,
    SnackBar
  },
  computed: {
    ...mapState(["barcodes"])
  }
})
export default class Scan extends Vue {
  // Mapped Variables ----------------
  // ---------------------------------
  barcodes!: any;

  // Local Variables -----------------
  // ---------------------------------
  cameraDetected: boolean = false;
  snackInit: boolean = false;
  valid: boolean = false;
  showScanBtn: boolean = true;
  scanBarName: string = "";
  window: any = {
    height: 0,
    width: 0
  };
  showVideo: boolean = false;
  scannedBarcodes: any = [];
  Quagga: any;

  // Computed ------------------------
  // ---------------------------------
  get excededBarcodeLimit() {
    return this.barcodes.length >= 20 ? true : false;
  }

  get barcodeFontSize() {
    if (this.window.width >= 4096) return "50"
    if (this.window.width >= 3840 && this.window.width <= 4095) return "40"
    if (this.window.width >= 2560 && this.window.width <= 3839) return "30"
    return "20"
  }

  get scannerWidth() {
    return this.window.width >= 897 ? 640 : 320;
  }

  get scannerHeight() {
    return this.window.width >= 897 ? 480 : 240;
  }

  get videoCenter() {
    let marginLeft: any = 0;

    if (this.window.width >= 897) {
      marginLeft = (this.window.width - 640) / 2;
      return `margin-left: ${marginLeft}px`
    }

    if (this.window.width <= 896) {
      marginLeft = (this.window.width - 320) / 2;
      return `margin-left: ${marginLeft}px`
    }
  }

  get stopScanCenter() {
    let marginLeft: any = 0;

    marginLeft = (640 -64) / 2;
    return `margin-left: ${marginLeft}px;
            margin-top: 20px;`
  }

  get nameRules() {
    let rules = [
      (v: any) => !!v || 'Name is required',
      (v: any) => (v && (v.length >= 1 && v.length <= 13)) || `Barcode name must be between 1 and 13 characters`,
    ];
    return rules;
  }

  // Watchers ------------------------
  // ---------------------------------
  @Watch('window.width')
  checkWindowResize() {
    if (this.showVideo) {
      this.stopScan();
    }
  }

  // Lifecycle Events ----------------
  // ---------------------------------
  created() {
    this.$store.dispatch("retrieveBarcodes");
    window.addEventListener('resize', this.handleResize)
    this.handleResize();
    this.checkUserCamera();
  }

  destroyed() {
    window.removeEventListener('resize', this.handleResize);
    if (this.showVideo) {
      this.stopScan();
    }
  }

  // Methods -------------------------
  // ---------------------------------
  reset() {
    this.scanBarName = "";
    this.scannedBarcodes = [];
    this.showVideo = false;
    this.showScanBtn = true;
  }

  startScan() {
    this.scannedBarcodes = [];
    this.showVideo = true;
    this.showScanBtn = false;

    Quagga.init({
      inputStream: {
        type: 'LiveStream',
        constraints: {
          width: this.scannerWidth,
          height: this.scannerHeight,
          facingMode: 'environment',
          aspectRatio: { min: 1, max: 2 },
        },
      },
      locator: {
        patchSize: 'medium',
        halfSample: true,
      },
      numOfWorkers: 2,
      frequency: 10,
      decoder: {
        readers: [
          // "code_128_reader",
          "ean_reader",
          // "ean_8_reader",
          // "code_39_reader",
          // "code_39_vin_reader",
          // "codabar_reader",
          // "upc_reader",
          // "upc_e_reader",
          // "i2of5_reader",
          // "2of5_reader",
          // "code_93_reader"
        ],
      },
      locate: true,
    }, function(err: any) {
      if (err) {
        return console.error(err);
      }
      Quagga.start();
    });
    Quagga.onDetected(this.onDetected);
    Quagga.onProcessed(this.onProcessed);
  }

  stopScan() {
    this.reset();
    Quagga.stop();
    this.showScanBtn = true;
  }

  async saveScan() {
    await this.$store.dispatch("saveBarcode", {
      barcode: this.buildBarcode()
    }).then(success => {
      this.reset();
      this.snackInit = true;
      setTimeout(() => {
        this.snackInit = false
      }, 2000);
    })
  }

  onDetected(result: any) {
    // Empty barcodes arr
    this.scannedBarcodes = [];    
    Quagga.stop();
    this.scannedBarcodes.push(result);
    this.showVideo = false;
  }

  onProcessed(result: any) {
    let drawingCtx = Quagga.canvas.ctx.overlay;
    let drawingCanvas = Quagga.canvas.dom.overlay;

    if (result) {
      if (result.boxes) {
        drawingCtx.clearRect(
          0,
          0,
          parseInt(drawingCanvas.getAttribute('width')),
          parseInt(drawingCanvas.getAttribute('height'))
        );
        result.boxes
          .filter(function(box: any) {
            return box !== result.box;
          })
          .forEach(function(box: any) {
            Quagga.ImageDebug.drawPath(box, { x: 0, y: 1 }, drawingCtx, {
              color: 'green',
              lineWidth: 2,
            });
          });
      }
      if (result.box) {
        Quagga.ImageDebug.drawPath(result.box, { x: 0, y: 1 }, drawingCtx, {
          color: '#00F',
          lineWidth: 2,
        });
      }

      if (result.codeResult && result.codeResult.code) {
        Quagga.ImageDebug.drawPath(
          result.line,
          { x: 'x', y: 'y' },
          drawingCtx,
          { color: 'red', lineWidth: 3 }
        );
      }
    }
  }

  buildBarcode() {
    if (this.scannedBarcodes[0].codeResult.format === "ean_13") {
      this.scannedBarcodes[0].codeResult.format = "EAN13";
    }

    const bar = {
      id: uuidv4(),
      name: this.scanBarName,
      type: this.scannedBarcodes[0].codeResult.format,
      value: this.scannedBarcodes[0].codeResult.code
    };
    return bar;
  }

  checkUserCamera() {
    navigator.mediaDevices.enumerateDevices()
      .then((devices: any) =>{
        for (let d of devices) {
          if (d.kind === "videoinput") {
            this.cameraDetected = true;
          }
        }
      })
      .catch((err: any) => {
        console.log(err.name + ": " + err.message);
      });
  }
  
  handleResize() {
    this.window.width = window.innerWidth;
    this.window.height = window.innerHeight;
  }
};
